@font-face {
  font-family: 'Helvetica';
  src: url("fonts/HelveticaNowText-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Helvetica';
  src: url("fonts/HelveticaNowText-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal; }

body {
  font-family: Helvetica, sans-serif;
  font-size: 15px; }

